"use client";

import React, { createContext, useContext, useEffect, useState } from "react";

type User = { name?: string; email: string } | null;

type AuthContextType = {
  user: User;
  login: (email: string, password: string) => Promise<boolean>;
  signup: (name: string, email: string, password: string) => Promise<boolean>;
  logout: () => void;
};

const AuthContext = createContext<AuthContextType | undefined>(undefined);

const USERS_KEY = "ftx_users"; // stores { email: { password, name } }
const CURRENT_KEY = "ftx_current";

function readUsers(): Record<string, { password: string; name?: string }> {
  try {
    const raw = localStorage.getItem(USERS_KEY);
    return raw ? JSON.parse(raw) : {};
  } catch (e) {
    return {};
  }
}

function writeUsers(u: Record<string, { password: string; name?: string }>) {
  localStorage.setItem(USERS_KEY, JSON.stringify(u));
}

export function AuthProvider({ children }: { children: React.ReactNode }) {
  const [user, setUser] = useState<User>(null);

  useEffect(() => {
    try {
      const raw = localStorage.getItem(CURRENT_KEY);
      if (raw) setUser(JSON.parse(raw));
    } catch (e) {
      // ignore
    }
  }, []);

  async function login(email: string, password: string) {
    const users = readUsers();
    const record = users[email.toLowerCase()];
    if (record && record.password === password) {
      const u = { email, name: record.name };
      setUser(u);
      localStorage.setItem(CURRENT_KEY, JSON.stringify(u));
      return true;
    }
    return false;
  }

  async function signup(name: string, email: string, password: string) {
    const users = readUsers();
    const key = email.toLowerCase();
    if (users[key]) return false; // already exists
    users[key] = { password, name };
    writeUsers(users);
    const u = { email, name };
    setUser(u);
    localStorage.setItem(CURRENT_KEY, JSON.stringify(u));
    return true;
  }

  function logout() {
    setUser(null);
    localStorage.removeItem(CURRENT_KEY);
  }

  return (
    <AuthContext.Provider value={{ user, login, signup, logout }}>
      {children}
    </AuthContext.Provider>
  );
}

export function useAuth() {
  const ctx = useContext(AuthContext);
  if (!ctx) throw new Error("useAuth must be used within AuthProvider");
  return ctx;
}
